@use '../abstract/breakpoints' as *;
@use '../abstract/colors' as *;
@use "../abstract/fonts" as *;
@use '../abstract/functions' as *;
@use '../abstract/mixins' as *;

.main{
    @include relative-hidden;
}

.btn-wrapper{
    @include relative-hidden;
    display: flex;
}


.section-header{
    max-width: 72ch;
    margin-inline:auto;
    &--about{
        max-width: 80ch;
        @include breakpoint-xxl{
            max-width:91ch;
        }
    }
}

.section-p{
    max-width: 100%;
    @include breakpoint-md{
        max-width: 80%;
    }
    @include breakpoint-xxl{
        max-width: 70%;
    }
}

.section-center-div{
    display: flex;
    flex-wrap: wrap;
    @include breakpoint-sm{
        gap: rem(16);
    }

    @include breakpoint-lg{
        gap: rem(20);
        flex-wrap: nowrap;
    }
    @include breakpoint-xxl{
        align-items: center;
    }
}

.overlay{
    position: fixed;
    inset: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    -webkit-backdrop-filter: blur(6px);
    backdrop-filter: blur(6px);
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    @include breakpoint-lg{
        display: none;
    }
    &.active{
        opacity: 1;
        visibility: visible;
    }
}
